*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:radial-gradient(
  circle at 50% 50%,
  rgb(1, 51, 9) 0%,
  rgb(0, 19, 0) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.task-container {
    background-color: rgb(30, 43, 30);
    margin: 8px 0;
    padding: 15px 20px;
    display: flex;
    border-radius: 5px;
    justify-content: space-between;
    color: rgb(211, 211, 211);
    align-items: center;
}

.task-title {
    cursor: pointer;
}

.remove-task-button {
    background-color: rgb(30, 43, 30);
    border: none;
    font-size: 16px;
    color: chartreuse;
    cursor: pointer;
    margin: 0 5px;
}

.buttons-container {
    width: 30%;
    display: flex;
    justify-content: flex-end;
}

.see-task-details-button {
    background-color: rgb(30, 43, 30);
    border: none;
    font-size: 16px;
    color: chartreuse;
    cursor: pointer;
    font-weight: bold;
    margin: 0 5px;
}
.button {
    background-color: chartreuse;
    height: 40px;
    padding: 0 10px;
    border-radius: 5px;
    width: 100%;
    font-size: 16px;
    color: #444;
    font-weight: bold;
    cursor:  pointer;
    border: none;
}

.button:hover {
    background-color: rgb(105, 209, 0);
}
.add-task-container {
    margin: 15px 0;
    width: 100%;
    display: flex;
}

.add-task-input {
    height: 40px;
    padding: 0 10px;
    border-radius: 5px;
    border: none;
    flex: 2 1;
    background-color: #444;
    color: #eee;
    font-size: 16px;
}

.add-task-button-container {
    margin-left: 10px;
    flex: 1 1;
}
.task-details-container {
    background: #444;
    margin: 15px 0;
    padding: 15px 20px;
    display: flex;
    flex-direction: column;
    color: #eee;
    border-radius: 5px;
}

.task-details-container h2 {
    margin-bottom: 5px;
    color: chartreuse;
}

.back-button-container {
    width: 30%;
    margin-top: 10px;
}
.container {
    background-color: rgba(15, 15, 15, 0.700);
    max-width: 500px;
    margin: 30px auto;
    overflow: auto;
    min-height: 300px;
    border: 2px solid rgb(8, 8, 8);
    padding: 30px;
    border-radius: 10px;
}
